
*{margin:0;padding:0;
}
.listLogin{background: #eee;height:2rem;line-height: 2rem;
}
.prO{width:100%;height:5rem;background:-webkit-linear-gradient(#4444dd,#3399cc);background:linear-gradient(#4444dd,#3399cc);
}
.prT{width:100%;height:5rem;background:-webkit-linear-gradient(#3399cc,#ddd);background:linear-gradient(#3399cc,#ddd);
}
.po{left:50%;top:50%;width:5rem;height:5rem;margin-left:-2.5rem;margin-top:-2.5rem;border-radius: 50%;border:1px solid #ccc;overflow: hidden;
}
.prT span{display:block;padding-top:2.8rem;font-size: 1rem;color:#f40;
}
.mint-cell-wrapper{text-align: left;
}
.listLogin{width:100%;border-bottom:1px solid #dbdbdb;
}
.listLogin p{text-align: left;width:90%;margin:0.6rem auto;font-size:0.9rem;
}
.listLogin p i{font-size: 0.9rem;
}
.listLogin p span{display:inline-block;width:82%;
}



