.out{
	-webkit-transition:all 2s infinite;
	transition:all 2s infinite;
	-moz-animation: all 2s infinite;
	-webkit-animation: all 2s infinite;
	-o-animation: all 2s infinite;
}
.transformed{
	transform:rotate(60deg);
	-moz-transform:rotate(60deg);
	-webkit-transform:rotate(60deg);
	-o-transform:rotate(60deg);
}
.animateImg
{
animation: myfirst 5s;
-moz-animation: myfirst 5s;	/* Firefox */
-webkit-animation: myfirst 5s;	/* Safari  Chrome */
-o-animation: myfirst 5s;	/* Opera */
}
/* @keyframes myfirst
{
from {background: red;}
to {background: yellow;}
}

@-moz-keyframes myfirst Firefox
{
from {background: red;}
to {background: yellow;}
}

@-webkit-keyframes myfirst Safari  Chrome
{
from {background: red;}
to {background: yellow;}
}

@-o-keyframes myfirst Opera
{
from {background: red;}
to {background: yellow;}
} */
@keyframes myfirst
{
0%   {background: red;
}
25%  {background: yellow;
}
50%  {background: blue;
}
100% {background: green;
}
}
@-webkit-keyframes myfirst /* Safari  Chrome */
{
0%   {background: red;
}
25%  {background: yellow;
}
50%  {background: blue;
}
100% {background: green;
}
}
	


